.NewColor {
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100vh;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background: -webkit-gradient(linear, left top, right top, from(red), color-stop(orange), color-stop(yellow), color-stop(green), color-stop(cyan), color-stop(blue), to(violet));
  background: -webkit-linear-gradient(left, red, orange, yellow, green, cyan, blue, violet);
  background: -o-linear-gradient(left, red, orange, yellow, green, cyan, blue, violet);
  background: linear-gradient(to right, red, orange, yellow, green, cyan, blue, violet);
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.NewColor form {
  width: 60%;
  background-color: white;
  padding: 50px;
  border-radius: 12px;
}

.NewColor form div {
  padding: 15px;
}



